const getCprYears = () => {
  const months = ['01', '02', '03', '04', '05', '06', '07', '08', '09','10','11','12']
  const years = ['2017', '2018', '2019', '2020', '2021']
  let dates: string[] = []
  years.forEach(year => months.forEach(month => dates.push(`${year}-${month}`)))
  return dates
}

